import React,{useEffect,useState} from 'react'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import {Grid} from '@material-ui/core';
import {domain} from '../../../connection/Config'
import Rejected from './Rejected';
import Divider from '@material-ui/core/Divider';
import '../../../css/orders.css'
import firebase from '../../../connection/firebase' 

export default function Orders_popup({setPopupTriger,setId_order,id_order,newOrder,token}) {
    const [open, setOpen] = React.useState(false);
    
    const [date,setDate]=useState()
    const [time,setTime]=useState()
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const db = firebase.firestore(); 
    useEffect(() => {
        setOpen(true);
    }, [])
     
    
      const delivery= async(e)=>{
        e.preventDefault()
        let update_time;
       if(time && date){
         update_time={
           Date:date,
           Time: time
         }
       }
        try{
          const res2 = await fetch(`${domain}/orders/delivery`,
          {
              method: 'POST',
              headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
              },
              body: JSON.stringify(
                  {
                      id:token,
                      items:newOrder[0],
                      user_id:newOrder[0].user_id
                  }
                  ) 
          }
          )
         const data= await res2.json()
          console.log(data)
       if(data.user && data.cooker){
        let snapshot=db.collection('deliveries').add({
          delivery_price:15,
          delivery_status:'בדיקה',
          driver_uid:"",
          drop_off_address:newOrder[0].address||"",
          drop_off_geo_point:newOrder[0].location||"",
          drop_off_name:newOrder[0].name||"",
          drop_off_phone:data.user.phone||"",
          drop_off_timestamp:"",
          order_id:newOrder[0].id,
          pickup_adress:data.cooker.address||"",
          pickup_geo_point:data.cooker.geo_point,
          pickup_name:data.cooker.vendor||"",
          pickup_time:data.cooker.phone_number||"",
          shipping_id:"",
          arrival_time:newOrder[0].arrival_time,
          update_arrival_time:update_time ||''
         })
       }
     
      
         
        }
        catch(err){
          console.log(err)
          alert('. פנו לתמיכה טכנית בתחתי העמוד. באג במערכת עמכם הסליחה')
        }
   
    
        setOpen(false);
        setPopupTriger(false)
      }


    
    return (
      <div>
      
        <Dialog
          fullScreen={fullScreen}
          open={open}
         
          aria-labelledby="responsive-dialog-title"
        >
            <form onSubmit={delivery}>
          <DialogTitle id="responsive-dialog-title">התקבלה הזמנה חדשה</DialogTitle>
          <p>במקרה ולא תוכלו לבצע את ההזמנה התקשרו לרום</p>
          <DialogContent>
          <DialogContentText>
             {newOrder[0].items.map(item=>{
               return(
                <h3>.{item.amount}  {item.name}   </h3>
               )
               
             })
             }
           </DialogContentText>
           <Divider  style={{marginBottom:'5px',marginTop:'10px'}} variant="right" />
           <DialogContentText style={{fontSize:'18px'}}>
             <h3> למתי : <span className='time'>{newOrder[0].arrival_time}</span></h3>
           <h3>צפי הגעת המשלוח : <span className='time'>{new Date(newOrder[0].delivery).toLocaleString()}</span></h3> 
        
           ? האם המשלוח יגיע בזמן
           : אם לא ציין בכמה זמן תתעכב
           </DialogContentText>
          
            <DialogContentText className='bt-warper'>
            <input onChange={(e)=>{setDate(e.target.value)}} style={{width:"130px",marginRight:'10px',height:'30px'}} type='date'/>
            <input  onChange={(e)=>{setTime(e.target.value)}} style={{width:"130px",marginLeft:'10px',height:'30px'}} type='time'/>
            </DialogContentText>
          </DialogContent>
             <Grid className='bt-warper'>
             <Rejected/>
            <Button   type='submit' className='agree' variant='outlined' >
             אישור
            </Button>
             </Grid>
            
            </form>
        </Dialog>
      </div>
    );
}
